











import Vue from 'vue';

export default Vue.extend({
  name: '',
  mounted() {
    this.drawWebCartesian();
  },
  methods: {
    drawWebCartesian() {
      const canvas = this.$refs.WebCartesian as HTMLCanvasElement;

      // 横线与竖线的是距
      const dx = 50;
      const dy = 50;

      // 初始坐标原点
      let x = 0;
      let y = 0;
      const w = canvas.width;
      const h = canvas.height;
      const ctx = canvas.getContext('2d');
      if (!ctx) return;

      // 单个步长所表示的长度

      let xy = 0;

      ctx.strokeStyle = '#eee';

      ctx.lineWidth = 1;

      // 画横线
      while (y <= h) {
        ctx.moveTo(x, y);
        ctx.lineTo(w, y);
        ctx.stroke();

        // 横坐标的数字
        // ctx.font = '1px Calibri';
        ctx.fillText(xy.toString(), x, y);
        y += dy;
        xy += dx;
      }

      // 画竖线
      y = 0;
      xy = 0;
      while (x <= w) {
        ctx.moveTo(x, y);
        ctx.lineTo(x, h);
        ctx.stroke();
        // 纵坐标的数字
        // ctx.font = '1px Calibri';
        ctx.fillText(xy.toString(), x, 10);
        x += dx;
        xy += dy;
      }
    },
  },
});
